@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);


*{
  padding:0;
  margin:0;
  color:white;
  font-family: 'Montserrat', sans-serif;
}

/* Header */
.title{
  grid-area: b;
  text-align: center;
  font-size:32px;
}
.headerContainer{
  display: grid;
  grid-template-columns: 20% 60% 20%;
  grid-template-areas:  "a b c";
  justify-content: center;
  background-color: #091540;
  color:white;
  align-items:center;
  padding:16px;
}
.searchContainer{
  grid-area: c;
  display: flex;
  flex-direction: row;
  color:white;
}
button{
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
  color:white;
}
button:focus {
  outline: none;
}
::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}
:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}
::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: white;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: white;
}
.logo{
  color:white;
}
a{
  text-decoration: none;
}
.searchInput{
  padding: 4px;
  background-color: #7692FF;
  border:none;
}
.searchInput:focus {
  outline: none;
}

/* Menu */
.menuList{
  list-style: none;
  display:flex;
  flex-direction: row;
  justify-content: space-around;
}
.menuContainer{
  background-color: #7692FF;

  font-size:28px;
  padding:16px;
}

/* Slider */
.slider{
  background-color: #ABD2FA;
}
.sliderContainer{
  background-color: #9C9990;
  position: relative;
  min-height: 64vh;
  background: url(/Car-dealer/static/media/photo-of-person-driving-769892.40bc8334.jpg) no-repeat center center fixed;
  background-size: cover;
  margin-right:8%;
  margin-left:8%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-left:8%;
  padding-right:8%;
}
.sliderContainer:before{
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(171, 210, 250, 0.8);
}
.sliderContainer section{
  color: #FFF;
  position: relative;
}
.sliderContainer h2{
  font-size:40px;
  color:black;
}
.sliderContainer h3{
  font-size:28px;
  padding-top:12%;
  padding-bottom:16%;
  color:black;
}
.slideButton{
  justify-self: center;
}
.moreButton{
  background-color: #7692FF;
  padding:24px;
  padding-left:32px;
  padding-right:32px;
  font-size:32px
}

/* Why */
.whySection{
  background-color: #3D518C;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding:2%;
}
.whySection section{
  background-color: #3D518C;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.whyText{
  border-top:1px solid white;
  padding-top:8px;
  font-size:24px;
}
.whyIcon{
  font-size:112px !important;
}

/* Footer */
footer{
  background-color: #1B2CC1;;
  padding-top: 1%;
  padding-bottom: 1%;
  font-size: 16px;
  width: 100%;
  text-align: center;
}

/* About*/
section.aboutSection{
  background-color: #ABD2FA;
  padding-left: 16%;
  padding-right: 16%;
}
.aboutImage{
  width:100%;
  height: auto;
}
.aboutImageContainer{
  width: 100%;
}
.aboutTextContainer *{
  color:black;
  text-align: center;
  padding: 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.aboutContent {
  background-color: white;
}
.aboutCallMail{
  display: flex;
  flex-direction: row;
}
.aboutCallButton, .aboutMailButton{
  background-color: #1B2CC1;
  padding:8px;
  padding-left: 16px;
  padding-right:16px ;
  color:white !important;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 64px;   
}
button.aboutCallButton *, button.aboutMailButton *{
  color:white !important;
}
.aboutCall, .aboutMail{
  width: 100%;
}

/* Offers */
.offerContainer{
  display: grid;
  grid-template-columns: 33% 33% 33%;
   grid-gap: 1.5rem;
   border-bottom: 1px solid #7692ff;
   padding-top:16px;
}
.offersContainer{
  background-color: white;
  padding-left:16%;
  padding-right:16%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.offerImage{
  width: 100%;
  height: auto;
}
.offers{
  background-color: #091540;
  display: flex;
  justify-content: center;
  padding-left:8%;
  padding-right:8%;
}

.offerDetails{
  font-size:24px;
}
ul.offersContainer *{
  color:black;
}
.offerText, .offerPrice{
  padding-top:8px;
}
.offerPrice{
  text-align:center;
}
.offerHeadName{
  display: flex;
  flex-direction: row;
}
.offerHeadBrand{
  padding-right: 8px;;
}
.seeOffer {
  color:white !important;
  background-color:#7692FF ;
  padding:8px;
  font-size: 16px;
  width: 90%;
  -webkit-animation: seeOffer 3s linear infinite;
  animation: seeOffer 3s linear infinite;
  transition: 0.3s;
}
.seeOffer:hover {
  color:#091540 !important;
  background-color:white ;
  border:2px solid #7692FF;
  font-weight: bold;
  }
@keyframes seeOffer {
  0% {width: 90%;}
 20% {width: 87%;}
 40% {width: 85%;}
 60% {width: 87%;}
 100% {width: 90%;}
}
@-webkit-keyframes seeOffer {
  0% {width: 90%;}
 20% {width: 87%;}
 40% {width: 85%;}
 60% {width: 87%;}
 100% {width: 90%;}
 }

 /* Car offer */
.carOffer{
  padding-left: 8%;
  padding-right: 8%;
  padding-top: 2%;
  padding-bottom: 2%;
  display: grid;
  grid-template-columns: 60% 40%;
  grid-template-areas: 
  "left right";
}
div.carOffer, div.carOffer > *{
  color:black;
}
.imagesContainer{
  width: 100%;
  grid-area:left;
}
.inActivePicture{
  display: none;
}
.offerImageSmallPicture{
  width: 33.3%;
  height: auto;
}
.offerImageSmallPicture:hover{
  cursor: pointer;
  transform: scale(1.02);
}
.offerImageBigPicture{
  width: 100%;
  height: auto;
}
article.textContainer *{
  color:black;
}
.textContainer {
  padding-left: 8px;
  padding-top: 8px;
  }
.carInfo{
  padding-bottom:8px;
  border-bottom: 2px solid #7692FF;
}.carPrice h2{
  color:#1B2CC1 ;
}
.carNameAndPrice{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.carDetails{
  padding-top:8px;
  padding-bottom:8px; 
  border-bottom: 2px solid #7692FF;
}
.carDetails h3{
  padding-top:8px;
  padding-bottom:8px;
}
.carDetails li{
  padding-top:4px;
  padding-bottom:4px;
  list-style: none;
}
.backToOffers{
  background-color:#1B2CC1;
  color:white !important;
  width:200px;
  padding:12px;
}

  /*Contact*/
.sellerContact{
  padding-top:8px;
  padding-bottom:16px;
}
.sellerContact h3{
  padding-top:8px;
  padding-bottom:8px;
}
.sellerContact label{
  padding-bottom:8px;
}
.mailContact{
  display: flex;
  flex-direction: column;
  width: 50%;
  }
.mailContact input{
  outline: 2px solid #7692FF;
}
.contacts{
  display: flex;
  flex-direction: row;
}
.contactTextArea{
  width: 100%;
  padding-top: 8px;
}
.contactTextArea textarea{
  width: 100%;
  outline: 2px solid #7692FF;
  box-sizing: border-box;
  height: 150px; 
  margin-top:8px;
}
.contactButton{
  width: 100%;
  background-color: #7692FF;
  color:white !important;
  padding: 16px;
  font-size:16px;
  margin-top:4px;
}
.carOfferWithButton{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom:16px; ;
}

